// layouts/AuthLayout.tsx
import React from 'react';
import { View, Text, StyleSheet } from 'react-native';

interface AuthLayoutProps {
  children: React.ReactNode;
  loading?: boolean;
  title?: React.ReactNode;
  footer?: React.ReactNode;
  imageBackground?: boolean;
  logoVisible?: boolean;
}

// 매우 단순화된 버전으로 수정
const AuthLayout: React.FC<AuthLayoutProps> = ({
  children,
  loading = false,
  title,
  footer,
  imageBackground = false,
  logoVisible = true,
}) => {
  return (
    <View style={styles.container} testID="auth-layout">
      {title && <View testID="title-container">{title}</View>}
      
      <View style={styles.contentContainer} testID="content-container">
        {loading ? <View testID="loading-indicator"><Text>로딩 중...</Text></View> : children}
      </View>
      
      {footer && <View testID="footer-container">{footer}</View>}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  contentContainer: {
    flex: 1,
    width: '100%',
  },
});

export default AuthLayout;